import { Component, OnInit } from '@angular/core';
import * as firebase from "firebase";

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent implements OnInit {
  title="demoApp";
  provider:any;
  user:any;

  constructor(){

  }
  
  ngOnInit(): void {
    var provider = new firebase.default.auth.GoogleAuthProvider();
    var test = new firebase.default.auth.FacebookAuthProvider()
    this.provider = provider;
    firebase.default.auth().onAuthStateChanged(user=> {
      this.user = user;
    });

  }
  logout(){
    firebase.default.auth().signOut().then(function() {
     console.log("sign out");
     
    }).catch(function(error) {
      // An error happened.
    });
  }
 
 
  loginWithGmail() {
    firebase.default.auth().signInWithPopup(this.provider).then(function(result) {
     var user = result.user;
     console.log(user.email);
     
    }).catch(function(error) {
     
      var errorCode = error.code;
      var errorMessage = error.message;
      var email = error.email;
      var credential = error.credential;
    });
  }
  
}